const showToast = function(title, icon = 'none', mask = false, duration = 2000){
	uni.showToast({
		title,
		icon,
		mask,
		duration
	})
}

/**
 * 
 * @param {*} callback 
 * @param {*} wait 
 * @returns 
 */
const throttle = function(callback, wait){
  let timer = 0;
  return () => {
    let now = new Date().getTime();
    if (now - timer > wait) { // 
       callback();
       timer = now; // 
    }
  }
}

export {
	showToast,
	throttle
}